<template>
  <div class="login">
    <div class="bg"></div>
    <Modal class="login-modal" v-model="loginModal" width="360"
           class-name="vertical-center-modal" :closable="false" :mask-closable="false">
      <div>
        <h2 style="margin:10px;color:#5cadff;text-align:center">
          <span>苏州衡量贸易有限公司</span>
        </h2>
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" >
          <Form-item prop="name">
            <Input v-model="formValidate.name" placeholder="请输入姓名"></Input>
          </Form-item>
          <Form-item prop="password">
            <Input v-model="formValidate.password" @keyup.native.enter="handleSubmit('formValidate')" type="password" placeholder="请输入密码"></Input>
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="primary" size="large" long :loading="modal_loading" @click="handleSubmit('formValidate')">登录</Button>
        <!--<small style="text-align: center;display:block" class="subText">@copyright by Artiely</small>-->
      </div>
    </Modal>
  </div>
</template>
<script>
  export default {
    name: 'login',
    data () {
      return {
        loginModal: true,
        modal_loading: false,
        formValidate: {
          name: '',
          password: ''
        },
        ruleValidate: {
          name: [
            {required: true, message: '姓名不能为空', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不能为空', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) { // login
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modal_loading = true
            this.$api.login(this.formValidate).then((res) => {
              if (res === 'success') {
                this.$Message.success('登陆成功')
                localStorage.token = this.formValidate.password
                this.$router.push('/index')
              } else {
                this.modal_loading = false
                this.$Message.error('登陆失败')
                this.$Notice.warning({
                  title: '登录提示',
                  desc: '用户名或密码错误',
                  duration: 2
                })
              }
            })
          } else {
          }
        })
      }
    }
  }
</script>





